<template lang="pug">
        anim.vertical-centered
</template>

<script>
import anim from "../assets/images_src/cyclist.svg";
import { TweenMax } from "gsap";

export default {
  components: {
    anim
  },
  mounted() {
    this.svg_target = document.getElementById("cyclist");
    var duration = 0.5;
    animateWheels(duration);
    animateLeg("left_leg", duration, 0);
    animateLeg("right_leg", duration, -1);
    animateBack(duration);
    animateHead(duration);
    animateArm(duration);

    function animateWheels(duration) {
      TweenMax.set(".wheel", { transformOrigin: "center", rotation: 0 });
      TweenMax.to(".wheel", duration, {
        rotation: "360_ccw",
        ease: Linear.easeNone,
        repeat: -1
      });
    }

    function animateArm(duration) {
      TweenMax.set("#arm", {
        transformOrigin: "10% 90%",
        rotation: 0
      });
      TweenMax.to("#arm", duration / 2, {
        rotation: "+=5",
        repeat: -1,
        delay: (duration / 2) * 0.1,
        yoyo: true,
        ease: Sine.easeInOut
      });
    }

    function animateHead(duration) {
      TweenMax.set("#head", {
        transformOrigin: "60% 85%",
        rotation: 0
      });
      TweenMax.to("#head", duration / 2, {
        rotation: "-=5",
        repeat: -1,
        delay: (duration / 2) * 0.1,
        yoyo: true,
        ease: Sine.easeInOut
      });
    }

    function animateBack(duration) {
      TweenMax.set("#back", {
        transformOrigin: "60% 85%",
        rotation: 0
      });
      TweenMax.to("#back", duration / 2, {
        rotation: "+=3",
        repeat: -1,
        delay: (duration / 2) * 0.1,
        yoyo: true,
        ease: Sine.easeInOut
      });
    }

    function animateLeg(id, duration, delay) {
      TweenMax.set("#" + id, {
        transformOrigin: "80% 10%",
        rotation: -20
      });
      TweenLite.set("#" + id + ">.calf", {
        transformOrigin: "7% 18%",
        rotation: -10
      });

      TweenMax.to("#" + id, duration, {
        rotation: "+=40",
        repeat: -1,
        delay: delay * duration,
        yoyo: true,
        ease: Sine.easeInOut
      });

      TweenMax.to("#" + id, duration, {
        y: 0.1,
        repeat: -1,
        delay: (0.25 + delay) * duration,
        yoyo: true,
        ease: Sine.easeInOut
      });

      TweenMax.to("#" + id + ">.calf", duration, {
        rotation: "+=50",
        repeat: -1,
        delay: (0.75 + delay) * duration,
        yoyo: true,
        ease: Sine.easeInOut
      });
    }
  }
};
</script>

